@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&family=Source+Sans+Pro:wght@400;600;700&display=swap);
.header{max-width:800px;margin:0 auto 25px;padding:25px;background-color:rgba(255,255,255,0.05);color:white;border:3px solid white;box-shadow:0px 0px 10px black;border-radius:10px;text-align:center}.header h1{margin-bottom:10px;text-shadow:1px 1px 1px black}.Quiz{max-width:800px;margin:0 auto 25px;padding:25px;background-color:rgba(255,255,255,0.05);color:white;border:3px solid white;box-shadow:0px 0px 10px black;border-radius:10px}.Quiz .statusBar{padding:0 5px 5px;margin-bottom:25px;border-bottom:3px solid white;display:flex;justify-content:space-between}.Quiz .statusBar div{display:flex}.Quiz .statusBar div .pulse{margin:0 10px;display:inline;-webkit-animation:pulse linear 1s 1;animation:pulse linear 1s 1}.Quiz .question-container .code{padding:5px;margin:10px 0;background-color:rgba(255,255,255,0.4);color:black;font-size:15px;border-radius:10px}.Quiz .options-container{display:flex;flex-direction:column;justify-content:center}.Quiz .options-container .option{height:100%;padding:5px;margin:5px 0;background-color:rgba(255,255,255,0.15);border-radius:10px;font-weight:600}.Quiz .options-container .option:hover{background-color:rgba(255,255,255,0.6)}.Quiz .options-container .option input{margin-right:10px;cursor:pointer}.Quiz .options-container .checked{background-color:rgba(255,255,255,0.6)}.Quiz .options-container button{align-self:center}.Quiz .Score h2{text-align:center;margin-bottom:25px}@-webkit-keyframes pulse{0%{-webkit-transform:scale(1);transform:scale(1)}50%{-webkit-transform:scale(1.5);transform:scale(1.5)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes pulse{0%{-webkit-transform:scale(1);transform:scale(1)}50%{-webkit-transform:scale(1.5);transform:scale(1.5)}100%{-webkit-transform:scale(1);transform:scale(1)}}.home{max-width:800px;margin:0 auto 25px;padding:25px;background-color:rgba(255,255,255,0.05);color:white;border:3px solid white;box-shadow:0px 0px 10px black;border-radius:10px}.home p{margin-bottom:20px}.home button{margin:15px auto 0;padding:5px 10px;background-color:rgba(255,255,255,0.35);color:black;font-weight:600;font-family:'Source Sans Pro', sans-serif;display:flex;justify-content:center;align-items:center;border:2px solid white;box-shadow:0px 0px 5px black;border-radius:10px;cursor:pointer}.home button:hover{background-color:rgba(255,255,255,0.6)}.home button span{margin-right:5px}*{margin:0;padding:0;box-sizing:border-box}body{width:100vw;height:100vh;background:linear-gradient(to bottom right, #50a3a2 0%, #53e3a6 100%);font-family:'Source Code Pro', sans-serif;font-size:20px;overflow-x:hidden}body #root{width:100%;padding:25px}body #root button{margin:15px auto 0;padding:5px 10px;background-color:rgba(255,255,255,0.35);color:black;font-weight:600;font-family:'Source Sans Pro', sans-serif;display:flex;justify-content:center;align-items:center;border:2px solid white;box-shadow:0px 0px 5px black;border-radius:10px;cursor:pointer}body #root button:hover{background-color:rgba(255,255,255,0.6)}body #root .bg-bubbles{position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1}body #root .bg-bubbles li{position:absolute;list-style:none;display:block;width:40px;height:40px;background-color:rgba(255,255,255,0.15);bottom:0;-webkit-animation:square 25s infinite;animation:square 25s infinite;transition-timing-function:linear}body #root .bg-bubbles li:nth-child(1){left:10%}body #root .bg-bubbles li:nth-child(2){left:20%;width:80px;height:80px;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-duration:17s;animation-duration:17s}body #root .bg-bubbles li:nth-child(3){left:25%;-webkit-animation-delay:4s;animation-delay:4s}body #root .bg-bubbles li:nth-child(4){left:40%;width:60px;height:60px;-webkit-animation-duration:22s;animation-duration:22s;background-color:rgba(255,255,255,0.25)}body #root .bg-bubbles li:nth-child(5){left:70%}body #root .bg-bubbles li:nth-child(6){left:80%;width:120px;height:120px;-webkit-animation-delay:3s;animation-delay:3s;background-color:rgba(255,255,255,0.2)}body #root .bg-bubbles li:nth-child(7){left:32%;width:160px;height:160px;-webkit-animation-delay:7s;animation-delay:7s}body #root .bg-bubbles li:nth-child(8){left:90%;width:20px;height:20px;-webkit-animation-delay:15s;animation-delay:15s;-webkit-animation-duration:40s;animation-duration:40s}body #root .bg-bubbles li:nth-child(9){left:25%;width:10px;height:10px;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-duration:40s;animation-duration:40s;background-color:rgba(255,255,255,0.3)}body #root .bg-bubbles li:nth-child(10){left:55%;width:160px;height:160px;-webkit-animation-delay:11s;animation-delay:11s}@-webkit-keyframes square{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(-120vh) rotate(600deg);transform:translateY(-120vh) rotate(600deg)}}@keyframes square{0%{-webkit-transform:translateY(0);transform:translateY(0)}100%{-webkit-transform:translateY(-120vh) rotate(600deg);transform:translateY(-120vh) rotate(600deg)}}

